.contacts {
  &__info {
    margin-bottom: 24px;
 
    &--last {
      margin-bottom: 32px;
    }
  }
  
  &__info-title {
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
    margin-bottom: 8px;
  }
  
  &__info-link {
    text-decoration: none;
    color: inherit;
  }
  
  &__field {
    margin-bottom: 16px;
  
    &--last {
      margin-bottom: 32px;
    }
  }
}
  
.form-field {
  background: #f7f8ff;
  border-radius: 8px;
  border: 1px solid transparent;
  height: 48px;
  display: block;
  width: 100%;
  padding: 14px 16px;
  outline: none;
  
  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #8f99a8;
  }
  
  &:hover {
    border: 2px solid #ccd0e0;
  }
  
  &:focus {
    border: 2px solid $blue;
  }
  
  &--textarea {
    height: 144px;
    resize: none;
    font-family: inherit;
  }
}
