.testimonials {
  &__item {
    margin-bottom: 48px;
  
    &:last-child {
      margin-bottom: 0;
    }
  
    @include onDesktop {
      margin-bottom: 0;
    }
  }
}
  
.testimonial {
  @include onDesktop {
    margin-bottom: 0;
  }
  
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
  
    @include onDesktop {
      margin-bottom: 32px;
    }
  }
  
  &__photo {
    width: 100%;
    grid-column: 2 / 4;
  }
  
  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    margin: 0 0 4px;
  }
  
  &__role {
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
    margin-bottom: 4px;
  }
  
  &__about {
    margin: 0;
    text-align: center;
  }
}
