.gallery {
  &__place {
    margin-bottom: 32px;
  
    @include onDesktop {
      margin-bottom: 40px;
    }
  }
}
  
.place {
  &__photo-container {
    position: relative;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
  
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  
    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }
  
  &__photos {
    margin-bottom: 16px;
  
    @include onDesktop {
      margin-bottom: 24px;
    }
  }
  
  &__photo {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
  }
  
  &__link {
    font-weight: bold;
    color: $blue;
    text-decoration: none;
  }
}
